var Templify = {};
Templify.install = function(Vue, options) {
	options = options || {};
	options.name = options.name || "templified";
	Vue[options.name] = function(name) {
		switch(name) {
			case "create/form.html": return "<section class=\"view-create\" v-touch:swipe.prevent=\"shift\">\r\n\t<div class=\"errors\">\r\n\t\t<ul>\r\n\t\t\t<li v-for=\"error in errors\" v-on:click=\"dismiss(error)\">{{error?error.message:\"Null Error\"}}</li>\r\n\t\t</ul>\r\n\t</div>\r\n\r\n\t<div class=\"create-resource\">\r\n\t\t<h1>Synthesize Resource</h1>\r\n\t\t<gaze-form :fields=\"resourceFields\" :formData=\"resource\" :text=\"resourceText\" v-on:gaze-submit=\"submit($event)\"></gaze-form>\r\n\t\t<h2>Resource Fields</h2>\r\n\t\t<table class=\"aux-fields\">\r\n\t\t\t<tr v-for=\"field in Object.keys(resource.fields)\">\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<label>\r\n\t\t\t\t\t\t{{field}}\r\n\t\t\t\t\t\t<input type=\"text\" v-model=\"resource.fields[field]\" />\r\n\t\t\t\t\t</label>\r\n\t\t\t\t</td>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<button class=\"fas fa-minus ctrl\" v-on:click=\"removeField(resource, field)\"></button>\r\n\t\t\t\t</td>\r\n\t\t\t</tr>\r\n\t\t\t<tr>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<label>\r\n\t\t\t\t\t\tField Name\r\n\t\t\t\t\t\t<input type=\"text\" v-model=\"name\" v-on:keydown.enter=\"addField(resource, name); name = ''\" />\r\n\t\t\t\t\t</label>\r\n\t\t\t\t</td>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<button class=\"fas fa-plus ctrl\" v-on:click=\"addField(resource, name); name = ''\"></button>\r\n\t\t\t\t</td>\r\n\t\t\t</tr>\r\n\t\t</table>\r\n\t</div>\r\n\t\r\n\t<div class=\"create-relation\">\r\n\t\t<h1>Synthesize Relation</h1>\r\n\t\t<gaze-form :fields=\"relationFields\" :formData=\"relation\" format=\"lcars\" v-on:gaze-submit=\"submit(undefined, $event)\"></gaze-form>\r\n\t\t<h2>Relation Fields</h2>\r\n\t\t<table class=\"aux-fields\">\r\n\t\t\t<tr v-for=\"field in Object.keys(relation.fields)\">\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<label>\r\n\t\t\t\t\t\t{{field}}\r\n\t\t\t\t\t\t<input type=\"text\" v-model=\"relation[field]\" />\r\n\t\t\t\t\t</label>\r\n\t\t\t\t</td>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<button class=\"fas fa-minus ctrl\" v-on:click=\"removeField(relation, field)\"></button>\r\n\t\t\t\t</td>\r\n\t\t\t</tr>\r\n\t\t\t<tr>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<label>\r\n\t\t\t\t\t\tField Name\r\n\t\t\t\t\t\t<input type=\"text\" v-model=\"name\" v-on:keydown.enter=\"addField(relation, name); name = ''\" />\r\n\t\t\t\t\t</label>\r\n\t\t\t\t</td>\r\n\t\t\t\t<td>\r\n\t\t\t\t\t<button class=\"fas fa-plus ctrl\" v-on:click=\"addField(relation, name); name = ''\"></button>\r\n\t\t\t\t</td>\r\n\t\t\t</tr>\r\n\t\t</table>\r\n\t</div>\r\n\t\r\n\t<div class=\"upload-resources\">\r\n\t\t<h1>Synthesize Resources (CSV)</h1>\r\n\t\t<div class=\"gaze csv-upload\">\r\n\t\t\t<input type=\"file\" class=\"upload\" ref=\"resourceCSV\" v-on:change=\"parseResourceCSV()\"/>\r\n\t\t\t<div class=\"preview\" v-if=\"csv.resources && csv.resources.length\">\r\n\t\t\t\t<table>\r\n\t\t\t\t\t<tr>\r\n\t\t\t\t\t\t<th>\r\n\t\t\t\t\t\t\tName\r\n\t\t\t\t\t\t</th>\r\n\t\t\t\t\t</tr>\r\n\t\t\t\t\t<tr v-for=\"r in csv.resources\">\r\n\t\t\t\t\t\t<td>\r\n\t\t\t\t\t\t\t{{r.name}}\r\n\t\t\t\t\t\t</td>\r\n\t\t\t\t\t</tr>\r\n\t\t\t\t</table>\r\n\t\t\t\t<input type=\"submit\" v-on:click.prevent=\"parse()\" />\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n\t\r\n\t<div class=\"upload-relation\">\r\n\t\t<h1>Synthesize Relations (CSV)</h1>\r\n\t\t<div class=\"gaze csv-upload\">\r\n\t\t\t<input type=\"file\" class=\"upload\" />\r\n\t\t\t<input type=\"submit\" v-on:click.prevent=\"parse()\" />\r\n\t\t</div>\r\n\t</div>\r\n</section>\r\n";
			case "csvparser/input.html": return "<div class=\"gaze csv-upload\">\r\n\t<input type=\"file\" class=\"upload\" />\r\n\t<input type=\"submit\" v-on:click.prevent=\"parse()\" />\r\n</div>\r\n";
			case "dashboards/create.html": return "\r\n<div>\r\n\t<!-- \r\n\t<button v-on:click.prevent=\"test()\">Test</button>\r\n\t<basic testData=\"22\" data-stuffing=\"tasty\" aaa=\"111\" aaa-bbb=\"-111\" dance=\"bullshit\">\r\n\t\t<h3 slot=\"carryOver\">Some Stuff</h3>\r\n\t\t<resource-map slot=\"map\" v-on:rerun=\"huh()\"></resource-map>\r\n\t\t<div slot=\"test\" slot-scope=\"content\">\r\n\t\t\t<span>Content: {{content}}</span>\r\n\t\t</div>\r\n\t</basic>\r\n\t -->\r\n\t \r\n\t<gazesynth></gazesynth>\r\n</div>\r\n";
			case "dashboards/experimental.html": return "\r\n<experiment>\r\n</experiment>\r\n";
			case "dashboards/list.html": return "<rs-panel>\r\n\t<!--resource-list name=\"main\">\r\n\t</resource-list-->\r\n</rs-panel>\r\n";
			case "dashboards/map.html": return "<resource-map>\r\n</resource-map>";
			case "dashboards/notfound.html": return "<h1>Not Found</h1>\r\n";
			case "experiments/basic.html": return "<section>\r\n\t<h3>Some Test</h3>\r\n\t<div v-touch:swipe.left.prevent=\"testLeft\">C: {{content}}</div>\r\n\t<div v-touch:swipe.right.prevent=\"testLeft\">D: {{dance}}</div>\r\n\t<button v-on:click=\"options()\">Test</button>\r\n\t<button v-on:click=\"testing()\">Console Data</button>\r\n\t<button v-on:click=\"debug()\">Debug</button>\r\n\t<button v-on:click=\"doubled()\">Doubled</button>\r\n\t<slot name=\"carryOver\"></slot>\r\n\t<h3 v-touch:swipe.prevent=\"testLeft\">After Slot</h3>\r\n\t<div id=\"info\">\r\n\t\t<span>Gaze:</span>\r\n\t\t<span>{{$root.g$Version}}</span>\r\n\t\t<span>@{{$root.g$Start}}</span>\r\n\t</div>\r\n\t<entry-resource :fields=\"fields\">\r\n\t</entry-resource>\r\n\t<slot name=\"test\" content=\"content\"></slot>\r\n\t<slot name=\"map\"></slot>\r\n</section>\r\n";
			case "experiments/experimental.html": return "<gaze-infini :records=\"records\">\r\n</gaze-infini>\r\n";
			case "form/basic.html": return "<section class=\"entry-formData\">\r\n\t<h1 v-if=\"text.title\">{{text.title}}</h1>\r\n\t\t\r\n\t<div v-if=\"errors && errors.length\" class=\"errors\">\r\n\t\t<h2 v-if=\"text.genErrorTitle\">{{text.genErrorTitle}}</h2>\r\n\t\t<div v-for=\"error in errors\" class=\"error\">{{error.message}}</div>\r\n\t</div>\r\n\t\r\n\t<!-- form v-on:submit.prevent=\"submit(formData)\" -->\r\n\t<form v-on:submit.prevent class=\"gaze-form\">\r\n\t\t<div v-for=\"field in fields\" class=\"field\" :class=\"field.class || field.name\">\r\n\t\t\t<label v-if=\"field.type === 'text' || field.type === 'date'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model=\"formData[field.key]\" />\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'number'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model.number=\"formData[field.key]\" />\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'checkbox'\">\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model=\"formData[field.key]\" />\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'select'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<select v-model=\"formData[field.key]\">\r\n\t\t\t\t\t<option v-for=\"option in options[field.key]\" v-bind:value=\"option.value\">\r\n\t\t\t\t\t\t{{option.display}}\r\n\t\t\t\t\t</option>\r\n\t\t\t\t</select>\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'textarea'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<textarea :placeholder=\"field.placeholder\" v-model=\"formData[field.key]\"></textarea>\r\n\t\t\t</label>\r\n\t\t\t<div v-if=\"field.type === 'radio'\" class=\"radio-set\">\r\n\t\t\t\t<label v-for=\"option in options[fild.key]\">\r\n\t\t\t\t\t<input :type=\"field.type\" v-model=\"formData[field.key]\" v-bind:value=\"option.value\" />\r\n\t\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t</label>\r\n\t\t\t</div>\r\n\t\t\t\r\n\t\t\t<div v-if=\"field.errors && field.errors.length\" class=\"errors\">\r\n\t\t\t\t<div v-for=\"error in field.errors\" class=\"error\">{{error.message}}</div>\r\n\t\t\t</div> \r\n\t\t</div>\r\n\t\t<div class=\"controls\">\r\n\t\t\t<h2 v-if=\"text.fieldErrorTitle\">{{text.fieldErrorTitle}}</h2>\r\n\t\t\t<button v-if=\"submittable\" v-on:click=\"submit(formData)\">{{text.submit}}</button>\r\n\t\t\t<button v-if=\"preservable\" v-on:click=\"preserve(formData)\">{{text.preserve}}</button>\r\n\t\t\t<button v-if=\"cancellable\" v-on:click=\"cancel(formData)\">{{text.cancel}}</button>\r\n\t\t</div>\r\n\t</form>\r\n</section>\r\n";
			case "form/lcars.html": return "<section class=\"entry-resource\">\r\n\t<span>LCARS</span>\r\n\t<form v-on:submit.prevent=\"submit(resource)\">\r\n\t\t<div v-for=\"field in fields\" class=\"field\" :class=\"field.class || field.name\">\r\n\t\t\t<label v-if=\"field.type === 'text' || field.type === 'date'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model=\"resource[field.key]\" />\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'number'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model.number=\"resource[field.key]\" />\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'checkbox'\">\r\n\t\t\t\t<input :placeholder=\"field.placeholder\" :type=\"field.type\" v-model=\"resource[field.key]\" />\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'select'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<select v-model=\"resource[field.key]\">\r\n\t\t\t\t\t<option v-for=\"option in options[field.key]\" v-bind:value=\"option.value\">\r\n\t\t\t\t\t\t{{option.display}}\r\n\t\t\t\t\t</option>\r\n\t\t\t\t</select>\r\n\t\t\t</label>\r\n\t\t\t<label v-if=\"field.type === 'textarea'\">\r\n\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t<textarea :placeholder=\"field.placeholder\" v-model=\"resource[field.key]\"></textarea>\r\n\t\t\t</label>\r\n\t\t\t<div v-if=\"field.type === 'radio'\" class=\"radio-set\">\r\n\t\t\t\t<label v-for=\"option in options[fild.key]\">\r\n\t\t\t\t\t<input :type=\"field.type\" v-model=\"resource[field.key]\" v-bind:value=\"option.value\" />\r\n\t\t\t\t\t<span>{{field.title}}</span>\r\n\t\t\t\t</label>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</form>\r\n</section>\r\n";
			case "infini/scroll.html": return "\r\n<div class=\"gaze-infini\">\r\n\t<div class=\"filter\">\r\n\t\t<label>\r\n\t\t\t<span>Filter List:</span>\r\n\t\t\t<input type=\"text\" placeholder=\"Filter for...\" />\r\n\t\t</label>\r\n\t</div>\r\n\t\r\n\t<div v-for=\"record in records\" :key=\"record.id\" class=\"record\" v-bind:class=\"{ loaded: record._loaded }\">\r\n\t\t<div v-if=\"!record._loaded\">{{record.id}} : {{!!record._loaded}}</div>\r\n\t\t<div v-if=\"record._loaded\">\r\n\t\t\t<slot name=\"recordDisplay\">\r\n\t\t\t\t<div class=\"name\">\r\n\t\t\t\t\t{{record.name}}\r\n\t\t\t\t</div>\r\n\t\t\t\t<div class=\"description\">\r\n\t\t\t\t\t<p>{{record.name}}</p>\r\n\t\t\t\t</div>\r\n\t\t\t</slot>\r\n\t\t</div>\r\n\t</div>\r\n</div>\r\n";
			case "panel/basic.html": return "<section class=\"rs-panel\">\r\n\t<div class=\"controls\">\r\n\t\t<div class=\"block top left bar dyn\">\r\n\t\t\t<button v-for=\"nav in topBar\" v-class=\"nav.classes\">\r\n\t\t\t\t<span v-if=\"nav.icon\" class=\"icon fa\" v-class=\"nav.icon\"></span>\r\n\t\t\t\t<span v-if=\"nav.prefix\" class=\"prefix\" v-class=\"nav.prefixClass\">{{nav.prefix}}</span>\r\n\t\t\t\t<span v-if=\"nav.indictor\" class=\"indicator\" v-class=\"nav.indicator\">{{nav.indicator}}</span>\r\n\t\t\t\t<span v-if=\"nav.text\" class=\"text\" v-class=\"nav.textClass\">{{nav.text}}</span>\r\n\t\t\t\t<span v-if=\"nav.suffix\" class=\"suffix\" v-class=\"nav.suffixClass\">{{nav.suffix}}</span>\r\n\t\t\t\t<span v-if=\"nav.ending\" class=\"icon fa\" v-class=\"nav.ending\"></span>\r\n\t\t\t</button>\r\n\t\t</div>\r\n\t\t<div class=\"block bottom left bar dyn\">\r\n\t\t\t<button v-for=\"nav in bottomBar\" v-class=\"nav.classes\">\r\n\t\t\t\t<span v-if=\"nav.icon\" class=\"icon fa\" v-class=\"nav.icon\"></span>\r\n\t\t\t\t<span v-if=\"nav.prefix\" class=\"prefix\" v-class=\"nav.prefixClass\">{{nav.prefix}}</span>\r\n\t\t\t\t<span v-if=\"nav.indictor\" class=\"indicator\" v-class=\"nav.indicator\">{{nav.indicator}}</span>\r\n\t\t\t\t<span v-if=\"nav.text\" class=\"text\" v-class=\"nav.textClass\">{{nav.text}}</span>\r\n\t\t\t\t<span v-if=\"nav.suffix\" class=\"suffix\" v-class=\"nav.suffixClass\">{{nav.suffix}}</span>\r\n\t\t\t\t<span v-if=\"nav.ending\" class=\"icon fa\" v-class=\"nav.ending\"></span>\r\n\t\t\t</button>\r\n\t\t</div>\r\n\t\t<div class=\"block top left column dyn\" v-bind:class=\"{'t':topBar && topBar.length, 'b':bottomBar && bottomBar.length}\">\r\n\t\t\t<button v-for=\"nav in leftColumn\" :class=\"nav.classes\">\r\n\t\t\t\t<span v-if=\"nav.icon\" class=\"icon\" :class=\"nav.icon\"></span>\r\n\t\t\t\t<span v-if=\"nav.prefix\" class=\"prefix\" :class=\"nav.prefix\">{{nav.prefix.prefix}}</span>\r\n\t\t\t\t<span v-if=\"nav.indictor\" class=\"indicator\" :class=\"nav.indicator\">{{nav.indicator.indicator}}</span>\r\n\t\t\t\t<span v-if=\"nav.text\" class=\"text\" :class=\"nav.text\">{{nav.text.text}}</span>\r\n\t\t\t\t<span v-if=\"nav.suffix\" class=\"suffix\" :class=\"suffix\">{{nav.suffix.suffix}}</span>\r\n\t\t\t\t<span v-if=\"nav.ending\" class=\"icon\" :class=\"nav.ending\"></span>\r\n\t\t\t</button>\r\n\t\t</div>\r\n\t\t<div class=\"block top right column dyn\" v-bind:class=\"{'t':topBar && topBar.length, 'b':bottomBar && bottomBar.length}\">\r\n\t\t\t<button v-for=\"nav in rightColumn\" v-class=\"nav.classes\">\r\n\t\t\t\t<span v-if=\"nav.icon\" class=\"icon fa\" v-class=\"nav.icon\"></span>\r\n\t\t\t\t<span v-if=\"nav.prefix\" class=\"prefix\" v-class=\"nav.prefixClass\">{{nav.prefix}}</span>\r\n\t\t\t\t<span v-if=\"nav.indictor\" class=\"indicator\" v-class=\"nav.indicator\">{{nav.indicator}}</span>\r\n\t\t\t\t<span v-if=\"nav.text\" class=\"text\" v-class=\"nav.textClass\">{{nav.text}}</span>\r\n\t\t\t\t<span v-if=\"nav.suffix\" class=\"suffix\" v-class=\"nav.suffixClass\">{{nav.suffix}}</span>\r\n\t\t\t\t<span v-if=\"nav.ending\" class=\"icon fa\" v-class=\"nav.ending\"></span>\r\n\t\t\t</button>\r\n\t\t</div>\r\n\t</div>\r\n\t<div class=\"container dyn\" v-bind:class=\"{'l':leftColumn && leftColumn.length, 'r':rightColumn && rightColumn.length, 't':topBar && topBar.length, 'b':bottomBar && bottomBar.length}\">\r\n\t\t<slot name=\"main\">\r\n\t\t</slot>\r\n\t</div>\r\n</section>\r\n";
			case "resourcemap/controls.html": return "<section class=\"map controls\">\r\n\t<button v-on:click=\"rerunLayout()\">\r\n\t\t<span class=\"fas fa-sync-alt\"></span>\r\n\t</button>\r\n</section>\r\n";
			case "resourcemap/map.html": return "\r\n<section class=\"resource map\">\r\n\t<div id=\"map\">\r\n\t\t<h1 v-on:click=\"rerunLayout()\">Resource Map</h1>\r\n\t\t<map-controls v-on:rerun=\"rerunLayout()\"></map-controls>\r\n\t</div>\r\n</section>\r\n";
			default: return null;
		}
	};
};
Vue.use(Templify);